@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR|Roboto&display=swap);
.movies {
  margin: 70px 50px;
  display: grid;
  grid-gap: 60px 30px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}

.movie {
  padding: 20px 20px 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
}

.movie__data {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 175px;
  z-index: 1;
}

.movie__info {
  width: 200px;
}

.movie__poster {
  position: relative;
  top: -40px;
  height: 200px;
}

.movie__poster img {
  height: 100%;
  border-radius: 10px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
}

.movie__title {
  margin-bottom: 10px;
  font-size: 1.5rem;
  font-weight: 600;
}

.movie__genres {
  margin-top: 20px;
  padding-left: 15px;
}

.movie__genres li {
  margin-top: 5px;
  list-style: square;
}

.movie__summary {
  position: relative;
  background-color: #fff;
  line-height: 1.3;
  z-index: 2;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

html,
body,
.main,
#root {
  height: 100%;
}

body {
  background-color: #333;
  font-family: "Roboto", "Noto Sans KR", serif;
}

.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 2rem;
  color: #666;
}

